.about__us__section {
  background-color: #212730;
  margin-top: 4.58em;
  padding: 2em 0 3em;
  color: #e2a60e;
}
.about__us__section > div {
  width: 90%;
  max-width: 1040px;
  margin: 0 auto;
}
.about__us__section h3 {
  font-size: 1.65em;
  font-family: "Nunito", sans-serif;
  position: relative;
  margin-bottom: 0.5em;
}
.about__us__section h3::after {
  content: "";
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 100px;
  height: 3px;
  background-color: currentColor;
  border-radius: 2px;
}

.first__section p,
.second__section p {
  font-family: "Noto sans", sans-serif;
  font-size: 1.1em;
  margin-bottom: 1em;
}
.first__section a,
.second__section a {
  color: currentColor;
}

.second__section p {
  margin-bottom: 3em;
}

.creator__section {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: #1a1d23;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}
.creator__image__wrapper {
  min-width: 100%;
  height: 24em;
  background-image: url("../site-images/me.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
.creator__details__wrapper {
  padding: 1em;
  min-width: 100%;
}
.creator__details__wrapper h4 {
  font-size: 1.5em;
  font-family: "Nunito", sans-serif;
  position: relative;
  margin-bottom: 0.5em;
}
.creator__details__wrapper h4::after {
  content: "";
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 80px;
  height: 2px;
  background-color: currentColor;
  border-radius: 2px;
}
.creator__details__wrapper p {
  color: var(--color-white);
  font-family: "Noto sans", sans-serif;
  font-size: 1.1em;
  margin-bottom: 0.75em;
}
.creator__details__wrapper p a {
  color: currentColor;
}

/******************
MEDIA QUERIES
******************/
@media screen and (min-width: 720px) {
  .creator__section {
    grid-template-columns: repeat(2, 1fr);
  }

  .creator__image__wrapper {
    height: 100%;
  }
}